import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AppConfig } from 'src/app/app-settings';

@Injectable({
  providedIn: 'root'
})
export class EditUserService {

  constructor(private httpClient: HttpClient) { }

  editUser(userData) {
    return this.httpClient.put(AppConfig.API_ENDPOINT + '/api/edit', userData);
  }
}
