import { DeleteUserService } from '../services/DeleteUser/delete-user-service.service';
import { GetUserService } from '../services/GetUser/get-user-service.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';

declare var $: any;

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  constructor(
    private getUserService: GetUserService,
    private deleteUserService: DeleteUserService,
    private router: Router) { }

  users = [];
  numberOfUsers = 0;
  errorMsg;
  confirmationString;
  modalKey;
  sortFlag = 0;
  searchText;

  ngOnInit() {
    this.getUserService.getAllUsers().subscribe(
      (data: any) => {
        this.users = data.users;
        this.errorMsg = null;
        this.numberOfUsers = this.users.length;
      },
      errorMsg => {
        this.errorMsg = errorMsg;
      }
    );
  }

  get allUsers() {
    console.log('USERS');
    console.log(this.users);
    return this.users;
  }

  deleteUser(id) {
    this.deleteUserService.deleteUser(Number(id)).subscribe({
      next: data => {
        this.numberOfUsers -= 1;
        this.users.splice(_.findIndex(this.users, ['id', id]), 1);
        $('#confirmModal').modal('hide');
        this.router.navigate(['/']);
      },
      error: errorMsg => {
        this.errorMsg = errorMsg;
      }
    });
  }

  openModal(user) {
    this.modalKey = user.id;
    this.confirmationString = 'User ' + user.name;
    $('#confirmModal').modal('show');
  }

  sortUsersBy(field: string) {
    this.users = _.sortBy(this.users, [ obj => obj[field] ]);
    if (this.sortFlag === 0) {
      this.sortFlag = 1;
    } else {
      this.sortFlag = 0;
      this.users = _.reverse(this.users);
    }
  }
}
