import { AddUserService } from '../services/AddUser/add-user-service.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { dateValidator } from '../validators/date-validator.validator';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {

  addUserForm;
  errorMsg;

  constructor(fb: FormBuilder, private addUserService: AddUserService, private router: Router) {
    this.addUserForm = fb.group({
      name: ['', Validators.required],
      age: ['', Validators.required],
      // dob: ['', [Validators.required, dateValidator]],
    });
  }

  ngOnInit() {
  }

  addUser() {
    this.addUserService.addUser(this.addUserForm.value).subscribe(
      success => {
        this.router.navigate(['/']);
      },
      errorMsg => {
        this.errorMsg = errorMsg;
      }
    );
  }

  get dob() {
    return this.addUserForm.get('dob');
  }

}
