import { GetUserService } from '../services/GetUser/get-user-service.service';
import { AddUserService } from '../services/AddUser/add-user-service.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { EditUserService } from '../services/EditUser/edit-user.service';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {

  editUserForm: FormGroup;
  name;
  age;
  dob;
  errorMsg;
  userKey;

  constructor(
    private editUserService: EditUserService,
    private route: ActivatedRoute,
    private router: Router,
    private getUserService: GetUserService,
    private fb: FormBuilder
    ) {}

  ngOnInit() {
    const id = this.route.snapshot.paramMap.get('id');

    this.getUserService.getUser(Number(id)).subscribe(
      (data: any) => {
        this.userKey = id;
        this.editUserForm = this.fb.group({
          name: [data.name, Validators.required],
          age: [data.age, Validators.required],
        });

      },
      error => this.errorMsg = error
    );
  }

  editUser() {
    const updatedData = this.editUserForm.value;
    updatedData.id = this.userKey;
    this.editUserService.editUser(updatedData).subscribe(
      success => this.router.navigate(['/']),
      error => this.errorMsg = error
    );
  }

}
